/* src/css/Project.css */

@font-face {
    font-family: 'HoltwoodOneSC';
    src: url('../font/HoltwoodOneSC-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NanumGothic';
    src: url('../font/NanumGothic.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

#project-wrapper {
    width: 100%;
    min-height: 970px;
    background-color: #FFF9E6;
    display: flex;
    justify-content: center;
    align-items: center;

    font-family: 'NanumGothic', sans-serif;
}

#project-container {
    max-width: 1600px;
    width: 90%;
    display: flex;
    gap: 60px;
    align-items: center;
    padding: 50px 0;
}

#project-image img {
    width: 764px;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

#project-info {
    flex: 1;
    color: #4E4E4E;
}

#project-number {
    font-weight: bold;
    margin-bottom: 10px;
    color: #B29480;
}

#project-title {
    font-size: 36px;
    margin: 0;
    color: #60493A;
}

#project-period {
    font-size: 24px;
    margin: 10px 0 20px 0;
    color: #60493A;
}

#project-description {
    font-size: 16px;
    line-height: 1.6;
    color: #60493A;
}