.profile-container {
    display: flex;
    width: 100%;
    max-width: 1600px;
    height: 970px;
    font-family: 'Noto Sans KR', sans-serif;
    background-color: #fff;
    color: #5a463a;
    padding: 40px;
    box-sizing: border-box;
    margin: 0 auto;


    align-items: center;
    justify-content: center;



}

.profile-left {
    width: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile-image {
    width: 200px;
    height: 200px;
    background-color: #d8d8d8;
    margin-bottom: 20px;
}

.profile-name {
    font-size: 24px;
    margin-bottom: 5px;
}

.profile-birth {
    font-size: 16px;
    margin-bottom: 20px;
}

.profile-contact div {
    margin: 10px 0;
    font-size: 16px;
}

.profile-right {
    width: 65%;
    padding-left: 40px;
    display: flex;
    /* 자식 요소들을 가로로 배치 */
    gap: 40px;
    /* profile-section과 skillsec 간격 */
}

/* profile-section과 skillsec 비율 조절 */
.profile-section {
    flex: 2;
    /* 왼쪽 영역 넓이 */
    margin-bottom: 0;
    /* 세로 간격 제거 */
}

.skillsec {
    flex: 1;
    /* 오른쪽 영역 넓이 */
}

.profile-section h3 {
    font-size: 20px;
    margin-bottom: 10px;
}

.profile-section p {
    margin: 3px 0;
    font-size: 16px;
}

.skill-bar {
    display: flex;
    align-items: center;
    margin: 5px 0;
}

.skill-bar span {
    width: 100px;
}

.skill-progress {
    flex: 1;
    background-color: #f9f1e6;
    border-radius: 10px;
    margin-left: 10px;
    position: relative;
    height: 20px;
}

.skill-fill {
    width: 100%;
    height: 100%;
    background-color: #ffe9b3;
    border-radius: 10px;
    text-align: right;
    padding-right: 5px;
    box-sizing: border-box;
    font-size: 12px;
    line-height: 20px;
}