/* src/css/Main.css */

@font-face {
    font-family: 'HoltwoodOneSC';
    src: url('../font/HoltwoodOneSC-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NanumGothic';
    src: url('../font/NanumGothic.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

/* 기본 초기화 및 배경색 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

#main-wrapper {
    width: 100%;
    height: 970px;
    background-color: #FFF9E6;
}


/* 제목 스타일 */
#title {
    font-family: 'HoltwoodOneSC', sans-serif;
    font-size: 128px;
    color: #fff;
    text-align: center;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

