/* 소개글 */
#intro {
    font-family: 'NanumGothic', sans-serif;
    font-weight: normal;
    font-size: 40px;
    color: #60493A;
}

#profile-wrapper {
    display: flex;
    justify-content: center;
    /* 중앙 정렬 */
    align-items: center;
    gap: 20px;
    /* 텍스트-이미지 간격 */
    margin: 0 auto;
}

.side-text {
    font-family: 'NanumGothic', sans-serif;
    font-size: 36px;
    color: #60493A;
    text-align: center;
    white-space: pre;
    /* <br>만 줄바꿈 처리, 나머지는 한 줄 유지 */
    flex-basis: 300px;
    /* 좌우 텍스트 동일 폭 확보 */
    flex-grow: 1;
    /* 가용 공간에 맞춰 늘어나지만 내용 줄바꿈 안됨 */
}

#profileimg {
    flex-shrink: 0;
    /* 이미지 크기 유지 */
    width: 600px;
    height: auto;
}

#profileimg img {
    width: 100%;
    height: auto;
    object-fit: cover;
}